.backdrop {
  position: fixed;
  z-index: 200;
  overflow-y: scroll;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;

  background: rgba(17, 21, 24, 0.9);

  transition: opacity 250ms var(--cubic);
  &.is-open {
    opacity: 1;
    pointer-events: initial;
  }

  &.is-hidden {
    opacity: 0;
    pointer-events: none;
  }
}

.scroll-lock {
  overflow: hidden;
}
